html {
  position: relative;
  min-height: 100%;
}

body {
    border: 0px;
    margin: 0px;
    padding-bottom: 40px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.damynavbar-right {
    float: right;
    -webkit-box-orient: horizontal;
    flex-direction: row;
}

.damynavbar-right .nav-link {
    padding-right: .5rem;
    padding-left: .5rem;
}

span.daactivetext {
    color: yellow;
}

span.daactivetext:hover {
    color: #ffff99;
}

.da-pad-for-navbar {
    padding-top: 66px;
}

@media (max-width: 767px) {
    .danavbar-title {
	max-width: 70%;
	overflow: hidden;
    }
}

.da-page-header {
  padding-bottom: 9px;
  margin: 40px 0 20px;
  border-bottom: 1px solid #eee;
}

.daterm {
    cursor: pointer;
    color: #408E30
}

.daterm:hover {
    cursor: pointer;
    color: #408E30;
    text-decoration: none;
}

.dabackicon {
    background: none;
    cursor: pointer;
    border: none;
    margin: 0;
    padding: 0;
    margin-left: -5px;
}

a.dabackbuttoncolor:hover,
a.dabackbuttoncolor:focus {
    text-decoration: none;
}

.dabackbuttoncolor {
    -webkit-appearance: none;
}

.daback {
    margin-left: 0.1em;
}

.form-group.darequired .dawidecol:before {
    position: absolute;
    content: '*';
    color: red;
    top: 0rem;
    left: 0.2rem;
}

.form-group.darequired .danolabel:before {
    position: absolute;
    content: '*';
    color: red;
    top: 0rem;
    left: 0.2rem;
}

.form-group.darequired .form-label:after {
    content: '*';
    color: red;
    margin-left: 0.2em;
}

.form-group.darequired .da-form-label:after {
    position: absolute;
    content: '*';
    color: red;
}

@media (max-width: 767px) {
    .form-group.darequired .da-form-label:after {
	bottom: 0.7rem;
    }
}

@media (min-width: 768px) {
    .form-group.darequired .da-form-label:after {
	top: 5px;
	right: 5px;
    }
}

.form-group.darequired.greyedout .da-form-label:after {
    display: none;
}

.form-group.dagreyedout .da-form-label {
    color: #999999;
}

.dawide .form-group.darequired .da-form-label:after {
    bottom: 0.7rem !important;
    top: auto !important;
    right: auto !important;
}

.dahuge {
  font-size: 3rem;
}

.daiconfloat {
    image-orientation: from-image;
    float: right;
}

.daclear {
    clear: both;
}
